@import '../assets/sass/mixins/_mixins.sass';


.theme__switcher {
  background: linear-gradient(135deg, #FF8955 0%, #C956A0 53.01%, #7245FA 99.83%);
  height: 40px;
  width: 68px;
  position: relative;
  border-radius: 60px;
  padding: 1px;

  &-inner {
    background: #1E1E20;
    position: relative;
    display: flex;
    height: 100%;
    border-radius: 6rem;
    color: $white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 .4rem;
    cursor: pointer;
  }

  &-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 6rem;
    z-index: 10;
    cursor: pointer;
  }

  &-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.8rem;
    min-width: 2.8rem;
    height: 2.8rem;
    border-radius: 50%;

    &-active {
      background: linear-gradient(135deg, #FF8955 0%, #C956A0 53.01%, #7245FA 99.83%);
      box-shadow: 0px 1px 0px #DF6A83, 0px 3px 5px rgba(0, 0, 0, 0.13), 0px 4px 10px rgba(253, 53, 71, 0.38);
    }
  }
}

// light theme
.page_theme_light {
  .theme__switcher {
    &-inner {
      background: #fff;
    }

    &-icon {
      &:deep(path) {
        fill: #231F20;
      }

      &-active {
        &:deep(path) {
          fill: #fff;
        }
      }
    }
  }
}