
.f-input {
    display: block;
    padding: 1.2em 2rem;
    background: transparent;
    @include font(1.6rem, 2.4rem, 500);
    color: #fff;
    width: 100%;
    @include transition;
    background: #1E1E20;
    border-radius: 1rem;
    &__border {
        padding: 2px;
        width: 100%;
        border-radius: 1.2rem;
        background: linear-gradient(135deg, #FF8955 0%, #C956A0 53.01%, #7245FA 99.83%); }
    &:focus {
        outline: none; }
    &.error {}

    &::placeholder {
        color: rgba(#FFFFFF, .68); }
    &__error {
        @include font(1.3rem, 1.23em, 500);
        padding-top: .8rem;
        color: #ff0000;
        position: relative;
        dispaly: block;
        @include transition; }
    &__wrapper {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 2.4rem; }

    &__label {
        @include font(1.6rem, 2rem, 500);
        color: rgba(#FFFFFF, .68);
        display: block;
        margin-bottom: .8rem; } }

//dark mode
.page_theme_light {
    .f-input {
        background: #fff;
        color: #282A2E;
        @include transition;

        &::placeholder {
            color: rgba(#282A2E, .4); }
        &:focus {
            outline: none; }

        &__label {
            color: rgba(#282A2E, .68); } } }
