@import '../assets/sass/mixins/_mixins.sass';


.section-start {
  padding: 13rem 0 0;
  min-height: 83.6rem;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;

  @include lg {
    padding: 13rem 0 7.2rem;
    min-height: 0;
  }

  .relative {
    z-index: 10;
  }

  .pre-title {
    margin-top: 9rem;

    @include lg {
      margin-top: -2rem;
    }
  }

  h1 {
    @include font(9.2rem, 10rem, 900, 0.02em);
    margin-bottom: 1.6rem;
    color: $white;
    font-family: 'Druk Cyr';
    text-transform: uppercase;

    @include lg {
      font-size: 8.2rem;
    }

    @include md {
      @include font(5.4rem, 6rem, 900, 0.08em);
    }
  }
}

.start-btns {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;

  .btn {
    &:not(:last-child) {
      margin-right: 4.8rem;
    }
  }
}

.main {
  display: block;
  position: relative;
  overflow: hidden;
}

.start__image {
  position: relative;

  @include lg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
  }

  &:before {
    content: " ";
    position: absolute;
    left: 0;
    right: -100vw;
    bottom: 0;
    background: linear-gradient(180deg, rgba(30, 30, 32, 0) 0%, #1E1E20 100%);
    z-index: 3;
    height: 10rem;

    @include lg {
      top: 0;
      height: auto;
    }
  }

  img {
    @include lg {
      transform: translateX(250px);
      position: relative;
      z-index: 2;
    }
  }
}


// light theme
.page_theme_light {
  .section-start {
    h1 {
      color: #282A2E;
    }
  }

  .start__image {
    &:before {
      background: linear-gradient(180deg, rgba(243, 243, 251, 0) 0%, #F3F3FB 100%);
    }
  }
}