@import '../assets/sass/mixins/_mixins.sass';


.logo {
  display: flex;
  @include font(1.6rem, 2.4rem, 700);
  color: $white;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  text-transform: uppercase;

  span {
    padding-left: 1.2rem;
    color: $white;
  }
}

// light theme
.page_theme_light {
  .logo {
    color: #282A2E;
  }
}