.content {
  h1 {
    @include font(4.8rem, 6rem, 1000, 0.08em);
    margin: 0 0 2rem;
    color: $white; }
  h2, h3, h4, h5, h6 {
    @include font(2rem, 2.8rem, 700);
    margin: 1.2rem 0 1.6rem;
    color: $white; }
  p, ul, ol {
    @include font(1.4remx, 2.4rem, 500);
    color: rgba($white, 0.68);
    margin-bottom: 3.6rem;
    position: relative;
    z-index: 5; }
  ol {
    list-style-type: decimal;
    margin-left: 20px; }
  figure {
    margin: 1.2rem 0 4.8rem;
    display: block;
    position: relative;
    img {
      max-width: 100%  !important;
      width: 100%  !important;
      height: auto !important;
      position: relative;
      z-index: 10;
      border-radius: 2.4rem;
      @include lg {
        border-radius: 1.2rem; } }
    figcaption {
      margin-top: 8px;
      @include font(1.4remx, 2.4rem, 500);
      color: rgba($white, 0.68);
      display: block;
      width: 100%;
      text-align: center; } }
  li {
    margin-bottom: 3.2rem;
    position: relative;
    &:last-child {
      margin-bottom: 0; } }
  ul, ol {
    margin-bottom: 4.8rem; }
  ul {
    li {
      padding-left: 3rem;
      &:after {
        content: " ";
        display: block;
        width: 8px;
        height: 8px;
        background-color: rgba($white, 0.68);
        border-radius: 50%;
        position: absolute;
        top: 8px;
        left: 4px; } } }
  ul.list {
    li {
      &:after {
        width: 14px;
        height: 10px;
        background-color: rgba($white, 0.68); } } } }



// light theme
.page_theme_light {
  .content {
    h1 {
      color: #282A2E; }
    h2, h3, h4, h5, h6 {
      color: #282A2E; }
    p, ul, ol {
      color: rgba(#282A2E, 0.68); }
    figure {
      figcaption {
        color: rgba(#282A2E, 0.68); } }
    ul {
      li {
        &:after {
          background-color: rgba(#282A2E, 0.68); } } }
    ul.list {
      li {
        &:after {
          background-color: rgba(#282A2E, 0.68); } } } } }

