@import '../assets/sass/mixins/_mixins.sass';


.section-blog {
  padding: 6rem 0 10rem;
  overflow: hidden;
  position: relative;
  z-index: 1;

  .container {
    position: relative;
    z-index: 10;
  }
}

.round-blog {
  position: absolute;
  z-index: 2;

  &-1 {
    width: 41rem;
    height: 41rem;
    background: linear-gradient(135deg, #FF8955 0%, #C956A0 53.01%, #7245FA 99.83%);
    border-radius: 50%;
    top: 30rem;
    left: calc(50% + 35rem);

    @include lg {
      top: auto;
      width: 14rem;
      height: 14rem;
      bottom: 46rem;
      left: auto;
      right: -4rem;
    }
  }

  &-2 {
    @include lg {
      bottom: 7rem;
      width: 26rem;
      height: 26rem;
      background: linear-gradient(135deg, #FF8955 0%, #C956A0 53.01%, #7245FA 99.83%);
      border-radius: 50%;
      left: -7rem;
    }
  }
}

.blog {
  &__more {
    &:deep(.button) {
      width: 100% !important;
    }
  }

  &__tabs {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 2.8rem;

    @include sm {
      flex-wrap: nowrap;
      white-space: nowrap;
      overflow: scroll;
      -webkit-overflow-scrolling: touch;
      margin: 0 -1.5rem 2.8rem;
      padding: 0 1.5rem;

      &::-webkit-scrollbar-track {
        background-color: transparent;
      }

      &::-webkit-scrollbar {
        display: none;
        background-color: transparent;
      }

      &::-webkit-scrollbar-thumb {
        background-color: transparent;
      }
    }
  }

  &__tab {
    margin-bottom: 1.2rem;
    margin-right: 1.2rem;
  }
}

.blog {
  display: flex;
  width: 100%;
  height: 100%;
  border-radius: 3.2rem;
  padding-bottom: 2.4rem;

  @include lg {
    height: auto;
    margin-bottom: 1.2rem;
  }

  &-content {
    padding: 4rem 2rem 0 6rem;

    @include lg {
      padding: 0 3.2rem 3.2rem;
    }
  }

  &-item {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 3.2rem;
    background: linear-gradient(135deg, rgba(245, 247, 250, 0.12) 0%, rgba(245, 247, 250, 0.06) 52.14%, rgba(245, 247, 250, 0.0001) 100%);
    border: 1px solid rgba(245, 247, 250, 0.06);
    box-shadow: 0px 1px 1px rgba(9, 13, 20, 0.4), -4px -4px 8px rgba(224, 224, 255, 0.04), 8px 8px 24px rgba(9, 13, 20, 0.4);
    backdrop-filter: blur(108.731px);
    border-radius: 2.4rem;
    min-height: 51rem;



    &__img {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: -3.2rem -3.2rem 2.4rem;
      object-fit: cover;
      border-radius: 2.4rem 2.4rem 0 0;
      overflow: hidden;

      img {
        max-width: 100%;
        width: 100%;
      }
    }

    &__date {
      font-weight: 500;
      font-size: 1.4rem;
      line-height: 2rem;
      color: #FFFFFF;
      margin-bottom: 2.4rem;

      span {
        display: inline-block;
        background: rgba(224, 224, 255, 0.02);
        padding: 1.2rem 2.4rem;
        border-radius: 90px;
      }
    }


    &__title {
      font-weight: 700;
      font-size: 2rem;
      line-height: 2.8rem;
      color: #FFFFFF;
      margin-bottom: 1.2rem;
    }

    &__desc {
      font-weight: 500;
      font-size: 1.6rem;
      line-height: 2.4rem;
      color: #FFFFFF;
      opacity: 0.68;
      padding: 0 0 2.4rem;
    }

    &__link {
      margin-top: auto;
    }

    &__big {
      flex-direction: row;
      padding: 0;

      @include lg {
        flex-direction: column;
      }

      .blog-item__img {
        width: 384px;
        min-width: 384px;
        margin: 0;
        border-radius: 2.4rem 0 0 2.4rem;
        overflow: hidden;
        align-items: unset;

        @include xl {
          width: 200px;
          min-width: 200px;
          overflow: hidden;
          object-fit: cover;
        }

        @include lg {
          width: 100%;
          min-width: unset;
          margin: 0 0 2.4rem;
          object-fit: cover;
          border-radius: 2.4rem 2.4rem 0 0;
          overflow: hidden;
        }

        img {
          width: 384px;
          min-width: 384px;

          @include lg {
            width: 100%;
            min-width: 100%;
          }
        }
      }
    }
  }
}

// light theme
.page_theme_light {

  .blog {
    &-item {
      background: rgba(255, 255, 255, 0.4);
      border: 1px solid rgba(245, 247, 250, 0.06);
      box-shadow: 0px 1px 1px rgba(255, 255, 255, 0.8), -4px -4px 16px rgba(255, 255, 255, 0.24), 8px 8px 24px rgba(237, 237, 237, 0.8);

      &__date {
        color: #282A2E;

        span {
          background: rgba(40, 42, 46, 0.02);
        }
      }

      &__title {
        color: #282A2E;
      }

      &__desc {
        color: #282A2E;
      }
    }
  }
}