@import '../assets/sass/mixins/_mixins.sass';


.mobilemenu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  z-index: 250;
  display: flex;
  flex-direction: column;
  background: #1E1E20 url(../assets/images/bg/bg-mobile-dark.png) no-repeat 40% 0;
  padding-top: 8rem;
  align-items: center;

  &__soc {
    margin-top: auto;
    padding: 1.6rem;
    gap: 0 1.2rem;
    background: rgba(224, 224, 255, 0.06);
    border-radius: 4rem;
    margin: 2rem;
  }

  &__nav {
    flex-grow: 1;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;

    a {
      &:not(:last-child) {
        margin-bottom: 4rem;
      }
    }
  }

  &__langs {
    &:deep(.lang__drop) {
      top: auto;
      bottom: 100%;
    }
  }
}

// light theme
.page_theme_light {

  .mobilemenu {
    background: #fff url(../assets/images/bg/bg-mobile-light.png) no-repeat 40% 0;

    &__soc {
      background: rgba(40, 42, 46, 0.06);
    }

    &__nav a {
      color: #282A2E;
    }
  }
}