@import '../assets/sass/mixins/_mixins.sass';


.logo {
  margin-right: 4.8rem;

  @include xl {
    margin-right: 2.4rem;
  }
}

.mobile__action {
  display: flex;
  width: 4rem;
  height: 4rem;
  justify-content: center;
  align-items: center;
}

.nav {
  a {
    display: inline-block;
    @include font(1.4rem, 2.4em, 600);
    color: #FFFFFF;
    @include transition;
    opacity: 0.68;

    &:hover {
      opacity: 0.68;
    }

    &.router-link-exact-active {
      opacity: 1;
    }

    &:not(:last-child) {
      margin-right: 4.8rem;

      @include xl {
        margin-right: 2.4rem;
      }
    }
  }
}

.header {
  padding: 2.6rem 0 2.1rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 300;
  box-sizing: border-box;
  background: linear-gradient(135deg, rgba(245, 247, 250, 0.12) 0%, rgba(245, 247, 250, 0.06) 52.14%, rgba(245, 247, 250, 0.0001) 100%);
  border: 1px solid rgba(245, 247, 250, 0.06);
  box-shadow: 0px 1px 1px rgba(9, 13, 20, 0.4), -4px -4px 8px rgba(224, 224, 255, 0.04);
  backdrop-filter: blur(108.731px);

  @include lg {
    padding: .8rem 0;
  }

  &__soc {
    gap: 0 0.8rem;
  }
}

// light theme
.page_theme_light {
  .header {
    background: linear-gradient(135deg, rgba(245, 247, 250, 0.12) 0%, rgba(245, 247, 250, 0.06) 52.14%, rgba(245, 247, 250, 0.0001) 100%);
    border: 1px solid rgba(245, 247, 250, 0.06);
    box-shadow: 0px 1px 1px rgba(255, 255, 255, 0.4), -4px -4px 8px rgba(255, 255, 255, 0.04);
    backdrop-filter: blur(108.731px);
  }

  .nav {
    a {
      color: #282A2E;
    }
  }
}