/*CORE STYLES*/
@import "../mixins/_mixins.sass";

html {
  font-size: 62.5%; }

body {
  font-family: 'Montserrat', sans-serif;
  overflow-x: hidden;
  position: relative;
  font-variant: normal;
  font-feature-settings: normal;
  margin: 0;
  @include font(1.6rem, 2.4rem, 500);
  transition: background-color .1s ease-out;
  font-smooth: auto; }

.theapp {
  background: #1e1e20;
  transition: background .3s;
  &.page_theme_light {
    background: #F3F3FB; } }


img.lazy {
  opacity: 0;
  @include transition;
  &[lazy=loaded] {
    opacity: 1; } }

.title {
  @include font(4.8rem, 6rem, 900, .08em);
  margin-bottom: 1.6rem;
  color: $white;
  font-family: 'Druk Cyr';
  text-transform: uppercase; }

.desc {
  @include font(1.6rem, 1.5em, 500);
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 4rem; }
.back {
  display: inline-flex;
  background: linear-gradient(135deg, #FF8955 0%, #C956A0 53.01%, #7245FA 99.83%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  display: inline-flex;
  @include transition;
  &:hover {
    letter-spacing: .1rem; }
  img {
    margin-right: 1.2rem; } }

body {
  &.body-overflow {
    overflow: hidden; } }


// pre title text
.pre-title {
  margin: 0 0 1.6rem;
  @include lg {
    margin: 0 0 1.2rem; }
  span {
    display: inline-flex;
    padding: .6rem 1.2rem;
    color: #9C42F5;
    @include font(1.6rem, 1.2em, 700);
    background: #27273E;
    box-shadow: 0px 0px 1px rgba(20, 16, 41, 0.4), 0px 2px 4px -1px rgba(20, 16, 41, 0.24), 0px 8px 8px -4px rgba(20, 16, 41, 0.24);
    border-radius: 1.2rem;
    @include lg {
      background: rgba(224, 224, 255, 0.02);
      box-shadow: none;
      padding: 1.2rem 2.4rem;
      @include font(1.2rem, 1.6em, 500);
      color: #FFFFFF; } } }



// light theme
.page_theme_light {
  .theapp {
    background: #F3F3FB;
    transition: background .3s; }
  .title {
    color: #282A2E; }
  .desc {
    color: rgba(#282A2E, 0.6); }

  // pre title text
  .pre-title {
    span {
      background: rgba(255, 255, 255, 0.92);
      color: #282A2E;
      box-shadow: none;
      @include lg {
        background: rgba(255, 255, 255, 0.92);
        box-shadow: none;
        color: #282A2E; } } } }
