@import '../assets/sass/mixins/_mixins.sass';


.appfooter {
  padding: 0 0 2.2rem;

  // background: #150132;
  @include lg {
    padding-bottom: 3.2rem;
  }

  &-line {
    opacity: 0.1;
    border: 1px solid #FFFFFF;
    margin: 0 0 2.2rem;

    @include lg {
      margin-bottom: 3.2rem;
    }
  }
}

.appfooter {
  &-logo {

    @include lg {
      justify-content: center;
      margin-bottom: 2rem;
    }
  }

  &-soc {
    .soc {
      justify-content: center;

      &:deep(a) {
        &:not(:last-child) {
          margin-right: 1.6rem;
        }
      }
    }
  }
}

.author {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: $white;
  @include font(1.2rem, 2.4rem, 400);

  @include lg {
    justify-content: center;
    margin-top: 2rem;
  }

  a {
    font-weight: 600;
    display: inline-flex;
    align-items: center;
    justify-content: center;

    svg {
      margin-left: .8rem;
    }
  }
}



// light theme
.page_theme_light {
  .author {
    color: #282A2E;

    &:deep(path) {
      fill: rgba(40, 42, 46, 0.8);
    }
  }
}