@import '../assets/sass/mixins/_mixins.sass';


.soc {
  display: flex;
  align-items: center;
  justify-content: space-between;

  a {
    @include transition;
    min-width: 2.8rem;
    width: 2.8rem;
    height: 2.8rem;
    background: rgba(255, 255, 255, 0.06);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    @include transition;

    &:deep(path) {
      fill: $white;
    }

    &:hover {
      background: rgba(255, 255, 255, 0.12);
    }
  }
}

// light theme
.page_theme_light {
  .soc {
    a {
      background: rgba(40, 42, 46, 0.06);

      &:deep(path) {
        fill: #282A2E !important;
      }

      &:hover {
        background: rgba(40, 42, 46, 0.12);
      }
    }
  }
}