@import '../assets/sass/mixins/_mixins.sass';


.lang {
  position: relative;
  display: inline-block;

  &__switcher {
    background: linear-gradient(135deg, #FF8955 0%, #C956A0 53.01%, #7245FA 99.83%);
    height: 4rem;
    width: 10rem;
    position: relative;
    border-radius: 6rem;
    padding: 1px;

    &-active {
      .lang__switcher-icon {
        transform: rotate(180deg);
      }
    }

    &-inner {
      background: #1E1E20;
      position: relative;
      display: flex;
      height: 100%;
      border-radius: 6rem;
      color: $white;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 .4rem;
      cursor: pointer;
    }

    &-text {
      padding: 0 .8rem;
    }

    &-icon {
      @include transition;
    }

    &-flag {
      img {
        border-radius: .2rem;
      }
    }

    &-icon,
    &-flag {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 2.8rem;
      // &-active {
      //     background: linear-gradient(135deg, #FF8955 0%, #C956A0 53.01%, #7245FA 99.83%);
      //     box-shadow: 0px 1px 0px #DF6A83, 0px 3px 5px rgba(0, 0, 0, 0.13), 0px 4px 10px rgba(253, 53, 71, 0.38);
      // }
    }
  }

  &__drop {
    position: absolute;
    top: 100%;
    width: 10rem;
    background: linear-gradient(135deg, #FF8955 0%, #C956A0 53.01%, #7245FA 99.83%);
    padding: .1rem;
    border-radius: 2.4rem;

    &-inner {
      background: #1E1E20;
      position: relative;
      border-radius: 2.4rem;
      color: $white;
      display: flex;
      cursor: pointer;
      padding: 1.2rem 0;
      flex-direction: column;
    }

    &-line {
      padding: .4rem 1.3rem;
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }
  }
}

// light lang
.page_theme_light {
  .lang {
    &__switcher {
      &-inner {
        background: #fff;
        color: #231F20;
      }

      &-icon {
        &:deep(path) {
          fill: #231F20;
        }
      }
    }

    &__drop {
      &-inner {
        background: #fff;
        color: #231F20;
      }
    }
  }
}